const actions = {
  // UI Actions
  TAG_CLICKED : "TAG_CLICKED",
  QUERY_INPUT : "QUERY_INPUT",

  // Loading Listz
  LOAD_LISTZ : "LOAD_LISTZ",
  LOAD_LISTZ_FROM_URL: "LOAD_LISTZ_FROM_URL",

  // Mutations
  SET_LISTZ: "SET_LISTZ",
  SET_QUERY: "SET_QUERY",
  SET_LISTZ_URL_QUERY_STRING: "SET_LISTZ_URL_QUERY_STRING",

  // Commits (Activity)
  SET_COMMITS: "SET_COMMITS",
  LOAD_COMMITS: "LOAD_COMMITS",

  // Fields updated
  QUERY_UPDATED: "QUERY_UPDATED"
}

export default actions;


